<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="'報表'" ></C-Back-Nav>
    <section class="relative z-0 mt-[52px] p-3">
      <!-- 開發時候用 -->
      <!-- <template v-for="(item,index) in reportList"> -->
      <!-- 部版時候用 -->
      <template v-for="(item,index) in reportList">
        <div
          v-if="isAuthority(item.label)"
          :key="`reportList${index}`"
          @click="doRoute(item.name)"
          @keypress="doRoute(item.name)"
          class="my-3 rounded-md border border-solid bg-white px-2 py-1 text-left"
        >
          {{item.label}}
        </div>
      </template>
    </section>
  </main>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'priceList-index',
  description: '報表的首頁',
  metaInfo: {
    title: '名留',
    titleTemplate: '%s | 報表',
  },
  data() {
    return {
      reportList: [
        { label: '設計師日報表', name: 'Report-daily-report' },
        { label: '訂單紀錄', name: 'Report-bill' },
        { label: '抽成報表', name: 'Report-profit-cut' },
        { label: '互助日報表', name: 'Report-emp-earn' },
      ],
    };
  },
  mounted() {
  },
  methods: {
    doRoute(name) {
      this.$router.push({ name });
    },
  },
  computed: {
    ...mapState('userInfo', ['userAuthorizeList']),
    isAuthority() {
      return (name) => this.userAuthorizeList.find((item) => item.pageName === name && item.authority);
    },
  },
};
</script>

